a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

button {
  border: none;
}

.overlay {
  height: 0%;
  width: 100%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #f1b14a;
  overflow-y: hidden;
  transition: 0.5s;
  z-index: 100;
}

.overlay-content {
  position: relative;
  top: 11%;
  width: 100%;
  text-align: center;
}

.overlay-content h2 {
  color: black;
  margin-bottom: 77px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #4d774e;
  display: block;
  transition: 0.3s;
}

.overlay a:hover,
.overlay a:focus {
  color: black;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
  color: black;
  cursor: pointer;
}

@media screen and (max-height: 450px) {
  .overlay {
    overflow-y: auto;
  }
  .overlay a {
    font-size: 20px;
  }
  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}

